    }
  }

  #hexagon{
    height: 60px;
    width: 208px;
    margin-bottom: -2px;
    margin-left: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    background-color: #808080;
    cursor: pointer;
    clip-path: polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
  }

  #hexagon_intern{
    height: 56px;
    width: 204px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    background-color: #f0f0f0;

    transition: all 0.1s;
    clip-path: polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
  }
  #hexagon_intern:hover{
    background-color: #E5E5E5;
  }
  #hexagon_intern:active{
    background-color: #D8D8D8;
  }

  #infoVersion{
    /* background-color: gray; */
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 0px;
    border-top: 1px gray solid;
  }

</style>